@import '../../Styles/vars.scss';

.about {
	padding-top: 21rem;
	padding-bottom: 11rem;
	background-image: url('../../assets/images/about-bg.svg');
	background-repeat: no-repeat;
	background-size: cover;
	margin-top: -37rem;
	position: relative;
	z-index: 3;

	@media screen and (max-width: $large_tablet) {
		padding-bottom: 8rem;
	}

	@media screen and (max-width: $large_mobile) {
		background-image: url('../../assets/images/about-bg-mobile.svg');
		padding-top: 8.5rem;
		margin-top: -16rem;
	}

	&__wrap {
		display: flex;
		align-items: center;

		@media screen and (max-width: $large_tablet) {
			flex-direction: column;
		}
	}

	&__left {
		width: calc(100% - 45.5rem - 8.5rem);
		margin-right: 8.5rem;

		@media screen and (max-width: $large_tablet) {
			margin-right: 0;
			width: 100%;
			margin-bottom: 1rem;
		}

		> div {
			padding-bottom: 78.2%;

			@media screen and (max-width: $large_tablet) {
				padding-bottom: 83.2%;
			}
		}
	}

	&__right {
		max-width: 45.5rem;
		width: 100%;

		@media screen and (max-width: $large_tablet) {
			max-width: 100%;
		}
	}

	&__small-title {
		margin-bottom: 1.5rem;
	}

	&__simple-title {
		margin-bottom: 6.5rem;

		@media screen and (max-width: $large_tablet) {
			margin-bottom: 2.5rem;
		}
	}
}
